  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .header, .dark-mode .contenedor2 {
  background-color: #1a1a1a;
}
.dark-mode .navegacion, .dark-mode .navegacion-footer {
  background-color: gray;
}
.dark-mode .navegacion a, .dark-mode .navegacion-footer a {
  color: black;
}
.dark-mode .footer-seccion {
  background-color: #1a1a1a;
}
.dark-mode .formulario {
  background-color: #b3b3b3;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #333333;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: gray;
  color: #FFFFFF;
}
.dark-mode .formulario ::placeholder {
  color: #fbfbfb;
}
.dark-mode .barra {
  background-color: #e1e1e1;
}

/** UI **/
.header {
  background-color: #05e09c;
  padding: 1rem 0 5rem 0;
  size-adjust: 60;
}
.header img {
  align-items: center;
}

.titulo {
  color: #FFFFFF;
  font-weight: 700;
}

.contenido-header {
  align-items: center;
  size-adjust: 150;
  justify-content: space-between;
}

.logotype {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer-seccion {
  background-color: #05e09c;
  padding-top: 5rem;
}

.barra-footer {
  background-color: #05e09c;
}

.contenedor-footer {
  max-width: 1200rem;
  justify-content: space-between;
}

.navegacion-footer {
  background-color: #FFFFFF;
  display: flex;
  flex-direction: column;
  margin-bottom: 5%;
  text-transform: capitalize;
  justify-content: space-between;
}

.contender-footer a {
  display: flex;
  color: #05e09c;
  text-decoration: none;
  font-size: 2rem;
  font-weight: bold;
  padding: 1rem;
  margin: 0;
  justify-content: space-between;
}

.contenedor-footer a:hover {
  background-color: #05e09c;
  color: #000000;
}

.copyright {
  display: flex;
  flex-direction: column;
  color: #FFFFFF;
  font-size: 2rem;
  justify-content: center;
}

@media (min-width: 768px) {
  .navegacion-footer {
    flex-direction: row;
    justify-content: space-between;
  }
}
.navegacion-footer a {
  display: flex;
  color: #05e09c;
  text-decoration: none;
  font-size: 2rem;
  font-weight: bold;
  padding: 1rem;
  margin: 0;
  justify-content: center;
}

.navegacion-footer a:hover {
  background-color: #05e09c;
  color: #FFFFFF;
}

p {
  text-align: center;
}

.contenedor {
  max-width: 800rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 2rem;
}

.dark-mode-boton {
  width: 4rem;
  filter: invert(100%);
}
.dark-mode-boton:hover {
  cursor: pointer;
}

.barra {
  display: flex;
  flex-direction: column;
  background-color: #FFFFFF;
  align-items: center;
  padding: 2rem;
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.4s ease-in-out, opacity 0.5s linear, height 1s ease-in-out linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #05e09c;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  font-weight: 700;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 3rem;
  }
}
.navegacion a:hover {
  background-color: #05e09c;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.iconsinternet:hover {
  cursor: pointer;
}

.formulario {
  display: grid;
  background-color: #e1e1e1;
  width: min(60rem, 100%);
  border-radius: 20px;
  margin: 0 auto;
  padding: 2rem;
  border-style: double;
}

.formulario fieldset {
  border: 2px;
}

.formulario legend {
  text-align: center;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 700;
  margin-bottom: 2rem;
  color: #000000;
}

@media (min-width: 768px) {
  .contenedor-campos {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: auto auto 8rem;
    column-gap: 1rem;
  }
}
.campo:nth-child(7) {
  grid-column: 1/3;
}

.campo:nth-child(8) {
  grid-column: 1/3;
}

.campo:nth-child(9) {
  grid-column: 1/3;
}

.campo {
  margin-bottom: 0rem;
}

.campo label {
  color: var(#000000);
  font-weight: 700;
  margin-bottom: 0.5rem;
  display: block;
  text-transform: capitalize;
}

.campo textarea {
  height: 14rem;
}

input:not([type=submit]),
textarea,
select {
  width: 100%;
  border: none;
  padding: 1rem;
  border-radius: 2rem;
}

.contacto {
  padding-bottom: 5rem;
}

.alinear-derecha {
  display: flex;
  justify-content: center;
  padding-top: 10rem;
}

.botonform {
  border-radius: 20rem;
  background-color: #b4fde6;
}

.botonform:hover {
  border-radius: 20rem;
  background-color: #FFFFFF;
  cursor: pointer;
}

/* Pasos */
.pasos-seguir {
  padding-top: 5rem;
  display: grid;
  text-align: center;
}

@media (min-width: 768px) {
  .pasos-seguir {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1rem;
  }
}
.paso {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.paso h2 {
  line-height: 2rem;
}

.paso .iconos {
  height: 15rem;
  width: 15rem;
  background-color: #bcf5a9;
  background-image: linear-gradient(to top, var(--verde2) 0%, var(--verde) 100%);
  border-radius: 50%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  border-style: double;
}

h3 {
  text-align: center;
}

h2 {
  text-align: center;
}

.contenedor3.sombra {
  padding-top: 10rem;
  text-align: center;
}
.contenedor3.sombra p {
  text-align: center;
}

.contenedor2 {
  max-width: 80rem;
  margin: 10rem auto 10rem;
  text-align: center;
}

/** Internas **/
.alerta {
  padding: 0.5rem;
  text-align: center;
  color: white;
  font-weight: 700px;
  text-transform: uppercase;
  margin: 1rem 0;
  border-radius: 50rem;
}
.alerta.error {
  background-color: #E08709;
}
.alerta.exito {
  background-color: #05e09c;
}